.showtime_dropdown {
  position: relative;
  display: inline-block;
}
.dropdown-content {
   display: none;
  position: absolute;
  background-color: #ffffff;
  min-width: 130px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}
.dropdown-content a {
  color: rgb(44, 212, 81);
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  font-size: 12px;font-weight: bold;
}
.showtime_dropdown:hover .dropdown-content {
    display: block;
    transition-duration: 0.2s all ease-in;
}
.showtime_dropdown > h4{
       color: rgb(18, 190, 18);
    padding:7px 10px;
    cursor: pointer;
}
